<template>
	<view class="address-wrap">
		<nav-bar :isBack="true">
			<template #navLeft>
				<view>
					订单页面
				</view>
			</template>
		</nav-bar>
		<view :style="{height:customNav.navBarHeight + 'px'}"></view>
		<!-- 用户没有收货地址时显示 -->
		<view class="empty-tip" v-if="isEmpty">
			<u-empty mode="address" icon="/static/image/common/address.png">
			</u-empty>
		</view>
		<!-- 显示用户收货地址 -->
		<view class="address-list" v-if="!isEmpty">
			
		</view>
	</view>
</template>

<script>
	const App = getApp().globalData
	import NavBar from '@/component/navbar/navbar.vue'
	import {
		selectAddressList
	} from '@/network/address/address.js'
	export default {
		components: {
			NavBar
		},
		data() {
			return {
				//判断用户是否没有地址列表
				isEmpty: false,
				//监听页面高度
				customNav: App.customNav,
				queryParams: {
					delFlag: 1, //没有删除的地址
				},
				//存放用户地址
				list: []
			}
		},
		onLoad() {
			this.addressList()
		},
		methods: {
			async addressList() {
				const {
					data: resList
				} = await selectAddressList(this.queryParams)
				if (resList.length <= 0) {
					this.isEmpty = true
				} else {
					this.list =  resList
				}
				console.log(resList)
				
			}
		}
	}
</script>

